<template>
    <div class="item">
        <input type="checkbox" id="item" :checked="todo.completed" @change="handleCheck()" />
        <!-- 也可以用v-model实现选择框与数据的同步，但不建议修改props -->
        <!-- <input type="checkbox" id="item" v-model="todo.completed" /> -->
        <label v-show="!todo.isEdit" for="item">{{ todo.title }}</label>
        <input
            v-show="todo.isEdit"
            type="text"
            :value="todo.title"
            @blur="handleConfirm($event)"
            @keyup.enter="handleConfirm($event)"
            ref="input"
        />
        <button @click="handleDelete()">删除</button>
        <button @click="handleEdit()" v-show="!todo.isEdit">编辑</button>
    </div>
</template>

<script>
export default {
    name: 'Item',
    data() {
        return {};
    },
    props: ['todo'],
    methods: {
        handleCheck() {
            this.$bus.$emit('checkTodo', this.todo.id);
        },
        handleDelete() {
            this.$bus.$emit('deleteTodo', this.todo.id);
        },
        handleEdit() {
            // if (this.todo.hasOwnProperty('isEdit')) {
            //     this.$set(this.todo, 'isEdit', true);
            // } else {
            //     this.todo.isEdit = true;
            // }
            this.$set(this.todo, 'isEdit', true);
            this.$nextTick(() => {
                this.$refs.input.focus();
            });
        },
        handleConfirm(event) {
            this.todo.isEdit = false;
            this.$bus.$emit('updateTodo', this.todo.id, event.target.value);
        }
    }
};
</script>

<style>
.item {
    width: 470px;
    height: 30px;
    line-height: 30px;
    padding: 0 5px;
}
.item label {
    margin: 5px;
}
.item button {
    display: none;
    float: right;
    height: 20px;
    margin: 5px 2px;
    line-height: 20px;
    padding: 0 5px;
    border: 1px solid #e15948;
    border-radius: 5px;
    color: white;
    background-color: #e15948;
}
.item:hover {
    background-color: #ddd;
}
.item:hover button {
    display: block;
}
button:active {
    background-color: #b34334;
}
</style>
