<template>
    <div class="root">
        <!-- 自定义事件 -->
        <Header @receive="receive"></Header>
        <List :todos="todos"></List>
        <Footer
            :todos="todos"
            :count="count"
            :isSelectAll="isSelectAll"
            @selectAllTodo="selectAllTodo"
            @deleteCompletedTodo="deleteCompletedTodo"
        ></Footer>
    </div>
</template>

<script>
import Header from './components/Header.vue';
import List from './components/List.vue';
import Footer from './components/Footer.vue';
// import pubsub from 'pubsub-js';

export default {
    name: 'App',
    components: {
        Header,
        List,
        Footer
    },
    data() {
        return {
            /* todos: [
                { id: '001', title: '抽烟', completed: true },
                { id: '002', title: '喝酒', completed: false },
                { id: '003', title: '烫头', completed: true },
                { id: '004', title: '学习', completed: false }
            ], */
            todos: JSON.parse(localStorage.getItem('todoList')) || [],
            count: { completedTodo: 0, allTodo: 0 },
            isSelectAll: false
        };
    },
    methods: {
        // 添加
        receive(newTodo) {
            this.todos.unshift(newTodo);
        },
        // 勾选
        checkTodo(id) {
            this.todos.forEach(function (todo) {
                if (todo.id === id) {
                    todo.completed = !todo.completed;
                }
            });
            // 检查事件数目，是否全选
            this.count.completedTodo = this.todos.reduce((pre, todo) => {
                return pre + (todo.completed ? 1 : 0);
            }, 0);
            if (this.count.completedTodo === this.count.allTodo) {
                this.isSelectAll = true;
            } else {
                this.isSelectAll = false;
            }
        },
        // 删除
        deleteTodo(id) {
            /* this.todos = this.todos.filter(function (todo) {
                return todo.id !== id;
            }); */

            // 简写，箭头函数
            /* this.todos = this.todos.filter((todo) => {
                return todo.id !== id;
            }); */

            // 简写，一个参数直接return的
            this.todos = this.todos.filter((todo) => todo.id !== id);
        },
        // 编辑
        updateTodo(id, title) {
            this.todos.forEach((todo) => {
                if (todo.id === id) {
                    todo.title = title;
                }
            });
        },
        // 全选
        selectAllTodo() {
            if (!this.isSelectAll) {
                this.todos.forEach((todo) => {
                    todo.completed = true;
                });
                this.isSelectAll = true;
            } else {
                this.todos.forEach((todo) => {
                    todo.completed = false;
                });
                this.isSelectAll = false;
            }
        },
        // 删除已完成任务
        deleteCompletedTodo() {
            this.todos = this.todos.filter((todo) => todo.completed === false);
        }
    },
    // 统计事件数目
    mounted() {
        this.count.completedTodo = this.todos.reduce((pre, todo) => {
            return pre + (todo.completed ? 1 : 0);
        }, 0);
        this.count.allTodo = this.todos.length;
        if (this.count.completedTodo === this.count.allTodo) {
            this.isSelectAll = true;
        }
        this.$bus.$on('checkTodo', this.checkTodo);
        this.$bus.$on('updateTodo', this.updateTodo);
        this.$bus.$on('deleteTodo', this.deleteTodo);
    },
    updated() {
        this.count.completedTodo = this.todos.reduce((pre, todo) => {
            return pre + (todo.completed ? 1 : 0);
        }, 0);

        this.count.allTodo = this.todos.length;
        if (this.count.allTodo === 0) {
            this.isSelectAll = false;
        }
    },
    // beforeDestroy时解绑事件
    // 本地存储
    watch: {
        todos: {
            deep: true,
            handler(value) {
                localStorage.setItem('todoList', JSON.stringify(value));
            }
        }
    }
};
</script>

<style>
* {
    margin: 0;
    padding: 0;
    border: 0;
}
.root {
    width: 500px;
    /* height: 300px; */
    /* border: 1px solid gray; */
    box-shadow: 0 0 2px 0.5px gray;
    margin: 50px;
    /* margin: 0 auto; */
}
</style>
