<template>
    <div class="footer" v-show="count.allTodo">
        <!-- <input type="checkbox" id="selectAll" @click="selectAllTodo()" :checked="isSelectAll" /> -->
        <!-- click控制初始化，checked控制更新。 -->
        <input type="checkbox" id="selectAll" @click="handleSelectAll()" :checked="isSelectAll" />
        <label for="selectAll">已完成{{ count.completedTodo }} / 全部{{ count.allTodo }}</label>
        <button @click="handleDeleteCompleted()">清除已完成任务</button>
    </div>
</template>

<script>
export default {
    name: 'Footer',
    data() {
        return {};
    },
    props: ['todos', 'count', 'isSelectAll'],
    methods: {
        handleSelectAll() {
            this.$emit('selectAllTodo');
        },
        handleDeleteCompleted() {
            this.$emit('deleteCompletedTodo');
        }
    }
};
</script>

<style>
.footer {
    width: 480px;
    height: 30px;
    padding: 10px;
    line-height: 30px;
}
.footer input {
    margin: 6px;
}
.footer button {
    float: right;
    height: 30px;
    padding: 3px;
    border: 1px solid;
    border-radius: 5px;
    color: white;
    background-color: #e15948;
}
button:active {
    background-color: #b34334;
}
</style>
