<template>
    <input type="text" :placeholder="msg" class="input" @keyup.enter="addTodo" />
</template>

<script>
import { nanoid } from 'nanoid';
export default {
    name: 'Header',
    data() {
        return {
            msg: '请输入你的任务名称，按回车键确认'
        };
    },
    methods: {
        // 按下回车添加事件
        addTodo(event) {
            // 获取用户输入1，利用数据对象
            // console.log(event.target.value);
            // 获取用户输入2
            // 利用v-model，将输入同步到data
            if (!event.target.value.trim()) return;
            const todo = { id: nanoid(), title: event.target.value, completed: false };
            // 利用自定义事件，在App.vue中定义receive以及回调函数receive。在此处触发事件，app中调用回调函数
            this.$emit('receive', todo);
            event.target.value = '';
        }
    }
};
</script>

<style>
.input {
    width: 480px;
    height: 30px;
    /* box-shadow: 0 0 2px 0.5px gray; */
    border: 1px solid #bababa;
    margin: 10px;
    text-indent: 1em;
}
</style>
